import React, { Component } from "react";
import Slider from "react-slick";

import styled from 'styled-components'

const Slide = styled.div`
  width: 300px !important;
  height: 404px;
  background: white;
`

export default class SlideShow extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <Slider
        {...settings}
        style={{
          marginBottom: 45,
          minHeight: 400,
        }}  
      >
        <Slide>
          <h3>1</h3>
        </Slide>
        <Slide>
          <h3>2</h3>
        </Slide>
        <Slide>
          <h3>3</h3>
        </Slide>
      </Slider>
    );
  }
}
