import * as React from "react";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";

export default function AnimateButton() {
    //   const [animationName, setAnimationName] = React.useState('')
      const style = {
        animationName: 'animationName',
        animationTimingFunction: "linear",
        animationDuration: "3s",
        animationDelay: "0.0s",
        animationIterationCount: 'infinite',
        animationDirection: "alternate",
        animationFillMode: "forwards"
      }

      const addStylesheetRules = (rules) => {
        var styleEl = document.createElement("style");
        document.head.appendChild(styleEl);
        var styleSheet = styleEl.sheet;
        styleSheet.insertRule(rules, 0);
      }

      const clickHdl = () => {
        let animationName = 'animationName'
        let keyframes = `
            @-webkit-keyframes ${animationName} {
                0% {-webkit-transform:translateY(12px)}
                50% {-webkit-transform:translateY(0px)}
                100% {-webkit-transform:translateY(-12px)}
            }`;
    
        addStylesheetRules(keyframes)
      }

    React.useEffect(() => {
        clickHdl()
    }, [])

  return (
    <Button variant="outlined" style={{maxWidth: 37, minWidth: 37, minHeight: 55, maxHeight: 55, padding: 0, border: '2px solid #FFFFFF', borderRadius: 15}}>
        <FiberManualRecordIcon
          style={style}
          sx={{ color: '#FFFFFF' }}
        />
      </Button>
  );
}
